.resumeWindow {
    font-family: "Helvetica Neue", sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 794px;
    height: 1123px;
    background-color: #f5f5f5;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    margin: 20px;
    border: #000000 1px solid;
    float: left;
    text-align: center;

}

.resumeWindow header {
    width:100%;
}
.resumeWindow h1 {
    font-size: 18px;
}

.resumeWindow p {
    font-size: 12px;
    margin-bottom: 5px;
}

.resumeWindow h2 {
    font-size: 16px;
    margin-left:5%;
    width:90%;
    text-align: left;
    border-bottom: #000000 1px solid;
}

.resumeWindow li {
    font-size: 12px;
    margin-bottom: 5px;
    text-align: left;
}
